import React, { Component, Fragment } from 'react';

import { Card, Row, Table, Popconfirm, Col } from 'antd';

import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';

import { studentTitles } from 'dump/data';

class StudentTitles extends Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.columns = [
      {
        title: 'Tên chức danh',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Mã chức danh',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Operation',
        dataIndex: 'operation',
        render: (text, record) =>
          studentTitles.length >= 1 ? (
            <Fragment>
              <BtnEdit href={`#/studentTitle/form/edit/${record.code}`} />
              <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
                <BtnDelete />
              </Popconfirm>
            </Fragment>
          ) : null,
      },
    ];
  }

  render() {
    return (
      <Card title="Danh sách chức danh">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/studentTitle/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={studentTitles} columns={this.columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <StudentTitles />
  </Page>
);



